.nav {
  background-color: #ae7be3;
  color: white;
  display: flex;
  justify-content: center;
  align-items: stretch;
  gap: 2rem;
  padding: 0 1rem;
  height: 4rem;
}

.nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  gap: 1rem;
}

.nav a {
  color: inherit;
  text-decoration: none;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 4rem;
}

.nav li {
  height: 100%;
}

.nav_active {
  background-color: #5cd3a8;
}

.nav li:hover {
  background-color: #a265e3;
}
